import React, { FC, useState } from 'react';
import { CommonProps, DownloadInfoProps, BtnTexts } from '@interface';
import { ItemClickType } from '@enum';
import { useDownloadInfo } from './useDownloadInfo';
import './index.less';
import btnHand from '../../assets/imgs/btn-hand.png';

const Button: FC<CommonProps> = (props = {}) => {
    const {
        schema: {
            className = '',
            size = 'nomal', // nomal | 正常大按钮; medium | 双btn; small | 小按钮
            key = 'texts',
            logInfo: { itemClickType = ItemClickType.END_CARD_BTN } = {},
            downloadingFormat = 'T...%',
            downloadPauseFormat = 'T%',
        },
        animationType = '', // animate-beat | 跳动；animate-beat | 抖动；animate-beat | 扫光
        style = {},
        info: {
            [key]: btnTexts = {
                adActionDescription: '',
                openAppLabel: '',
                installAppLabel: '',
            },
            isDownload = false,
            adActionDescription = '立即下载',
        },
    } = props;

    const downloadInfoOptions: DownloadInfoProps = {
        texts: btnTexts,
        isDownload,
    };

    if (downloadingFormat) {
        downloadInfoOptions.downloadingFormat = downloadingFormat;
    }
    if (downloadPauseFormat) {
        downloadInfoOptions.downloadPauseFormat = downloadPauseFormat;
    }

    const { btnText, isDownloading, progress, status } = useDownloadInfo(downloadInfoOptions);
    // getStatus && getStatus({ btnText, isDownloading, progress, status });

    return (
        <div
            data-itemClickType={itemClickType}
            className={`common-btn ${className} ${size} ${animationType}`}
            style={style}
        >
            {btnText}
        </div>
    );
};

export default Button;
