import { useState, useEffect, RefObject } from 'react';
import { registerProgressListener } from '@bridge';
import { BtnTexts, WateringStyles, DownloadInfoProps, BytesToMbProps } from '@interface';

function _initDownloadInfo(texts: BtnTexts, isDownload: boolean) {
    const btnText: string = texts.adActionDescription || (isDownload ? '点我试玩' : '查看详情');
    const status: number = 0;
    const progress: number = 0;
    const totalBytes: number = 0;
    const isDownloading: boolean = false;
    const downloadProgress: string = '0';
    return {
        status,
        progress,
        totalBytes,
        btnText,
        isDownloading,
        downloadProgress,
    };
}

// canvs 渲染 button 文字
const renderButtonText = (
    canvas,
    progress: number,
    actionBarText: string,
    styles: WateringStyles
) => {
    const c: HTMLCanvasElement = canvas.current;

    if (c as HTMLCanvasElement) {
        c.width = styles.width * 2;
        c.height = styles.height * 2;
        const fontSize = styles.fontSize || 15;
        const { width, height } = c;
        const currentWidth = width * progress;
        const textX = width / 2;
        const textY = height / 2 + 1;
        const ctx: CanvasRenderingContext2D = c.getContext('2d');
        ctx.font = `normal normal ${styles.fontWeight} ${fontSize}px Arial`;
        ctx.textAlign = 'center';
        ctx.textBaseline = 'middle';
        ctx.clearRect(0, 0, currentWidth, height);
        ctx.clearRect(currentWidth, 0, width, height);

        // 右边文字
        ctx.save();
        ctx.beginPath();
        ctx.rect(currentWidth, 0, width, height);
        ctx.clip();
        ctx.fillStyle = styles?.rightColor;
        ctx.fillText(actionBarText, textX, textY);
        ctx.restore();

        // 左边文字
        ctx.save();
        ctx.beginPath();
        ctx.rect(0, 0, currentWidth, height);
        ctx.clip();
        ctx.fillStyle = styles.leftColor;
        ctx.fillText(actionBarText, textX, textY);
        ctx.restore();
    }
};

export function useDownloadInfo({
    texts,
    isDownload,
    downloadingFormat = 'T...%',
    downloadPauseFormat = 'T%',
    watering,
    creativeId,
    isMulti = false,
}: DownloadInfoProps) {
    const [progressResult, setProgressResult] = useState({
        status: 0,
        progress: 0,
        totalBytes: 0,
    });
    const { status, progress } = progressResult;
    let btnText: string = '';
    const isDownloading: boolean = status === 2 || status === 3;
    useEffect(() => {
        if (isDownload) {
            const registerParam: {
                success: (param: any) => void;
                creativeId?: number;
                isMulti?: boolean;
            } = {
                success: (res) => {
                    setProgressResult(res.data);
                },
            };
            if (creativeId) {
                registerParam.creativeId = creativeId;
            }
            if (isMulti) {
                registerParam.isMulti = isMulti;
            }
            registerProgressListener(registerParam);
        } else {
            _initDownloadInfo(texts, isDownload);
        }
    }, []);
    const downloadProgress =
        Number((progress * 100).toFixed(0)) > 100 ? '100' : (progress * 100).toFixed(0);
    if (isDownload) {
        /*eslint-disable */
        switch (status) {
            case 0:
            case 1:
            case 4:
                btnText = texts.adActionDescription || '立即下载';
                break;
            case 2:
                btnText = `下载中...${downloadProgress}%`;
                break;
            case 3:
                btnText = '继续下载';
                break;
            case 5:
                btnText = texts.installAppLabel || '立即安装';
                break;
            case 6:
                btnText = texts.openAppLabel || '立即打开';
                break;
        }
        /* eslint-enable */
        if (status === 2 || status === 3) {
            const matches = {
                T: status === 2 ? '下载中' : status === 3 ? '继续下载' : btnText,
                '%': ` ${downloadProgress}% `,
                MB: bytestoMb({ ...progressResult, btnText }),
            };
            const format = status === 2 ? downloadingFormat : downloadPauseFormat;
            btnText = format.replace(/T|%|MB/g, (match) => matches[match] || '');
            if (watering) {
                const styles = watering?.styles;
                renderButtonText(watering?.canvas, progress, btnText, {
                    width: styles?.width,
                    height: styles?.height,
                    fontSize: styles?.fontSize,
                    fontWeight: styles?.fontWeight || 400,
                    rightColor: styles?.rightColor,
                    leftColor: styles?.leftColor,
                });
            }
        }
    } else {
        btnText = texts.adActionDescription || '点我试玩';
    }
    return {
        ...progressResult,
        btnText,
        downloadProgress,
        isDownloading,
    };
}

export const bytestoMb = ({ totalBytes, progress, status, btnText }: BytesToMbProps) => {
    let actionBarText: string = btnText;
    const totalMB: number = totalBytes / 1000.0 / 1000.0;
    const size: number = totalMB * progress;
    const curSize: string = `（${size.toFixed(2)}M/${totalMB.toFixed(2)}M）`;
    if (status === 2 || status === 3) {
        actionBarText = `${curSize}`;
    }
    return actionBarText;
};
